<template>
  <div>
    <Loadings v-if="list ==null"></Loadings>
    <div class="container scontainer" v-else>
      <div class="search-bar">
        <van-search
          v-model="keywords"
          show-action
          label="查询"
          placeholder="请输入搜索关键词"
          @search="searchData"
          clearable
        >
          <template #action>
            <div @click="searchData">搜索</div>
          </template>
        </van-search>
      </div>
      <Empty v-if=" list.length == 0"></Empty>
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
        class="list"
        v-else
      >
        <div
        
          class="item"
          v-for="(item, index) in list"
          :key="index"
        >
          <div class="flex-sb">
            <div class="info">
              <div class="company flex-sb">
                {{item.company}}
                <span class="price">销售金额:{{toThousands(item.total_sum)}}元</span>
              </div>
              <div class="contact">
                联系人:{{item.name}}
                <span>电话:{{item.phone == '' ?item.call :item.phone}}</span>
              </div>
              <div class="address">地址:{{item.address}}</div>
              <div class="address">业务员:{{item.sname}}</div>
            </div>
            <van-icon name="arrow" color="#eee" />
          </div>
        </div>
      </van-list>
    </div>
  </div>
</template>

<script>
import { getcustList } from "@api/public";
import { Icon, Search, Loading, List } from "vant";
import Empty from "@components/Empty";
import Loadings from "@components/Loadings";
export default {
  name: "CustList",
  components: {
    [Icon.name]: Icon,
    [Search.name]: Search,
    [Loading.name]: Loading,
    [List.name]: List,
    Empty,
    Loadings,
  },

  data: function () {
    return {
      list: [
        {
          cid: "13178",
          name: " 黄S",
          company: "深圳市锦龙自动化设备有限公司",
          call: "",
          total_sum: "580",
          address: "深圳市宝安区石岩街道北环路华丰圳宝工业园5栋2楼东",
          total_order: "1",
          phone: "15813870229",
          cust_no: "A866B58409464088998F792B44A2FDA7",
          staff_no: "A03",
          create_time: "1514908800",
          type: null,
          status: "合作中客户",
          visiting_time: "0",
          visiting_date: null,
          sname: "付明中",
          department: "业务部",
        },
        {
          cid: "12375",
          name: "刘韩芬（崔小姐）",
          company: "JAESHIN.CO",
          call: "",
          total_sum: "1420",
          address:
            "广州市白云区\n广园中路203-207号景泰西2巷2号102档（河北大厦后座）广州正鸿国际物流有限公司",
          total_order: "1",
          phone: "13825006605",
          cust_no: "B81B1783C7BE48759060FA667FF2EFF4",
          staff_no: "A03",
          create_time: "1537459200",
          type: null,
          status: "合作中客户",
          visiting_time: "0",
          visiting_date: null,
          sname: "付明中",
          department: "业务部",
        },
      ],
      keywords: "",
      type: "",
      page: "",
      cust: "",
      loading: false,
      finished: true,
    };
  },
  created() {},
  mounted: function () {
    this.type = this.$route.params.type;
    //this.getData();
  },
  methods: {
    searchData() {
      if (this.keywords != "") {
        this.page = "";
        this.cust = "";
      }
      this.getData();
    },
    toThousands(num) {
      var result = [],
        counter = 0;
      num = (num || 0).toString().split("");
      for (var i = num.length - 1; i >= 0; i--) {
        counter++;
        result.unshift(num[i]);
        if (!(counter % 3) && i != 0) {
          result.unshift(",");
        }
      }
      return result.join("");
    },
    getData() {
      getcustList({
        type: this.type,
        search: this.keywords,
        page: this.page,
        cust: this.cust,
      }).then((res) => {
        if (this.page != "" || this.cust != "") {
          this.list = this.list.concat(res.data);
        } else {
          this.list = res.data;
        }

        this.finished = res.data.length < 200 ? true : false;
        this.loading = res.data.length < 200 ? true : false;
        if (res.data.length == 200) {
          if (this.type == "small_cust" || this.type == "big_cust") {
            this.cust = this.list[this.list.length - 1].total_sum;
          } else {
            this.page = this.list[this.list.length - 1].cid;
          }
        }
      });
    },
    onLoad() {
      this.getData();
    },
  },
};
</script>
<style scoped>
.container {
  background: #fff;
}

.container .list {
  line-height: 43px;
  font-size: 14px;
}

.container .list .item {
  display: block;
  padding: 15px 8px;

  border-bottom: 10px solid #f9f9f9;
}

.container .list .item div {
  width: 100%;
}

.container .list .info {
  line-height: 18px;
  font-size: 12px;
}

.container .list .item .right {
  padding-right: 15px;
  font-weight: bold;
}

.container .list .item .right span {
  font-size: 10px;
  font-weight: 500;
}

.company {
  color: #333;
  font-size: 16px;
}

.contact {
  color: #999;
  margin: 5px 0;
  font-size: 12px;
}

.contact span {
  padding-left: 10px;
}

.address {
  color: #999;
  font-size: 12px;
  margin-bottom: 5px;
}

.list img {
  width: 30px;
}

.loading-tips {
  text-align: center;
  padding-top: 150px;
}
.info .price {
  font-size: 12px;
  color: #999;
}
</style>
